.btn {
	font-family: $font-family-helvetica-neue;

	&:not(.btn-sm):not(.btn-lg) {
		@include max-w(lg) {
			font-size: 17px;
			padding: 10px 25px;
		}
	}
}

.link-with-arrow {
	display: inline-block;
	font-size: 18px;
	transition: color 0.3s;

	&:after {
		@include border-arrow(6, 10, currentColor, r);

		content: '';
		display: inline-block;
		margin-left: 10px;
		vertical-align: middle;
		transition: border-color 0.3s;
	}
}