.card {
	&-blog {
		display: flex;
		flex-direction: column;
		background-color: $white;
		width: 100%;
		margin-bottom: 30px;
		transition: box-shadow 0.3s;

		@include media-breakpoint-up(sm) {
			margin-bottom: 40px;
		}

		@include media-breakpoint-up(lg) {
			margin-bottom: 60px;
		}

		&:hover {
			box-shadow: 0 0 20px rgba($secondary, 0.1)
		}

		&__img {
			@include responsive-ratio(16, 9);

			position: relative;
			display: block;
			overflow: hidden;

			&:after {
				@include absoluteCover;

				content: '';
				display: block;
				background-color: rgba($black, 0.2);
				opacity: 0;
				transition: opacity 1s;
			}

			img {
				transition: transform 1s;
			}

			&:hover {
				&:after {
					opacity: 1;
				}

				img {
					transform: scale(1.2);
				}
			}
		}

		&__body {
			display: flex;
			flex-direction: column;
			flex-grow: 1;
			padding: 20px 15px;
		}

		&__body-main-content {
			flex-grow: 1;
		}

		&__date {
			position: absolute;
			bottom: 0;
			left: 0;
			z-index: 5;
			display: inline-block;
			background-color: $primary;
			color: $white;
			font-size: 14px;
			font-weight: 700;
			padding: 2px 10px;
		}

		&__title {
			a {
				transition: color 0.3s;
			}
		}

		&__link-block {
			padding-top: 20px;
		}
	}
}