.bg-video-block {
	position: relative;

	&:after {
		@include absoluteCover;

		content: '';
		display: block;
		background-color: rgba($white, 0.7);
		z-index: 3;
	}

	&__html-bg-video {
		position: absolute;
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto;
		top: 50%;
		left: 50%;
		object-fit: cover;
		transform: translate(-50%,-50%);
		opacity: 0;
		transition: opacity 300ms linear;
		z-index: 2;

		&.is-visible {
			opacity: 1;
		}
	}
}