.footer {
	position: relative;
	padding: 40px 0 20px;
	background-color: $body-bg;

	@include media-breakpoint-up(sm) {
		padding: 60px 0 40px;
	}

	@include media-breakpoint-up(lg) {
		padding: 80px 0 60px;
	}

	&__bg {
		background-repeat: no-repeat;
		background-position: center bottom;
		background-size: contain;
		position: absolute;
		bottom: 35px;
		left: 0;
		right: 0;
		height: calc(100% + 500px);
		background-color: $body-bg;
		pointer-events: none;
	}

	&__wrap {
		position: relative;
		z-index: 5;
	}

	&__main-wrap {
		@include media-breakpoint-up(md) {
			display: flex;
			flex-wrap: wrap;
		}
	}

	&__main-contnet {
		@include media-breakpoint-up(md) {
			padding: 0 30px 0 0;
			flex-basis: 0;
			flex-grow: 1;
			max-width: 100%;
		}

		@include media-breakpoint-up(xl) {
			padding-left: 100px;
		}
	}

	&__navigation-content {
		@include media-breakpoint-up(md) {
			flex: 0 0 245px;
			max-width: 245px;
		}

		@include media-breakpoint-up(lg) {
			flex: 0 0 490px;
			max-width: 490px;
		}
	}

	&__logo {
		display: inline-block;
		max-width: 220px;
		margin-bottom: 45px;

		@include media-breakpoint-up(lg) {
			max-width: 305px;
		}
	}

	&__copyright-block {
		text-align: center;
		padding-top: 30px;
		font-size: 12px;
		font-family: $font-family-now-alt;
		color: $gray-400;

		@include media-breakpoint-up(lg) {
			padding-top: 60px;
		}
	}

	&__contacts-list {
		font-size: 19px;
		color: $gray-500;
		margin-bottom: 30px;
	}
}

.footer-widget {
	margin-bottom: 30px;

	&__title {
		color: $primary;
		font-family: $font-family-lit-sans;
		font-weight: 500;
	}

	&__text {
		@extend .address-text;
	}

	&__list {
		@include listreset;
	}

	&__list-item {
		display: flex;
	}

	&__list-link {
		position: relative;
		display: inline-block;
		font-size: 18px;
		color: $gray-500;
		line-height: 1.6;
		transition: color 0.3s;

		@include media-breakpoint-up(md) {
			font-size: 19px;
		}

		&:after {
			content: none;
			display: block;
			width: 6px;
			height: 6px;
			border-radius: 1px;
			background-color: $primary;
			position: absolute;
			top: 13px;
			right: -18px;
			opacity: 0;
			transform: translate(10px, 0);
			transition: transform 0.3s, opacity 0.3s;

			@include media-breakpoint-up(md) {
				content: '';
			}
		}

		&:hover {
			color: $primary;

			&:after {
				opacity: 1;
				transform: translate(0, 0);
			}
		}
	}
}