.header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: $zindex-fixed;
	background-color: rgba($white, 0.7);
	padding: 15px 0;
	transition: background-color 0.3s;

	@include media-breakpoint-up(lg) {
		padding: 30px 0;
	}

	@include media-breakpoint-down(lg) {
		min-height: 70px;
		display: flex;
		align-items: center;
	}

	&.active {
		background-color: $white;
	}

	&.menu-active {
		.header__navigation-block {
			@include max-w(lg) {
				transform: translate(0, 0);
			}
		}
	}

	&__main-content {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__logo {
		display: block;
		width: 180px;
		margin-right: 30px;

		@include media-breakpoint-up(sm) {
			width: 205px;
		}

		@include media-breakpoint-up(lg) {
			flex-shrink: 0;
		}

		@include media-breakpoint-up(xl) {
			margin-right: 0;
		}
	}

	&__navigation-block {
		@include media-breakpoint-up(xl) {
			padding: 0 30px;
		}

		@include max-w(lg) {
			position: fixed;
			top: 70px;
			right: 0;
			bottom: 0;
			height: calc(100vh - 70px);
			height: calc(var(--vh, 1vh) * 100 - 70px);
			background-color: $primary;
			width: 280px;
			transform: translate(100%, 0);
			transition: transform 0.7s cubic-bezier(.694,0,.335,1);
		}
	}

	&__navigation {
		@include max-w(lg) {
			padding: 40px 25px !important;
			height: 100%;
			overflow: auto;
		}
	}

	&__right-content {
		display: flex;
		align-items: center;

		@include media-breakpoint-up(xl) {
			justify-content: flex-end;
			width: 205px;
			flex-shrink: 0;
		}
	}

	&__hamburger-menu {
		display: flex;
		margin-left: 15px;

		@include media-breakpoint-up(lg) {
			display: none;
		}
	}

	&__reading-progress {
		position: absolute;
		top: 100%;
		left: 0;
		width: 0;
		height: 2px;
		background-color: $primary;

		@include media-breakpoint-up(sm) {
			height: 4px;
		}
	}
}

.header-navigation {
	@include listreset;

	line-height: 1.3;

	@include max-w(lg) {
		font-size: 24px;
	}

	@include media-breakpoint-up(lg) {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: flex-end;
	}

	@include media-breakpoint-up(xl) {
		justify-content: center;
	}

	&__item {
		@include media-breakpoint-up(lg) {
			padding: 7px;
		}

		&:not(:last-child) {
			@include max-w(lg) {
				margin-bottom: 10px;
			}
		}
	}

	&__link {
		display: inline-block;
		color: $white;

		@include media-breakpoint-up(lg) {
			position: relative;
			color: $black;
			padding: 0 5px;
		}

		&:before,
		&:after {
			content: none;
			background: $primary;
			position: absolute;
			width: 0;
			height: 2px;
			opacity: 0;
			transition: width 0.4s cubic-bezier(.694,0,.335,1), opacity 0.4s cubic-bezier(.694,0,.335,1);

			@include media-breakpoint-up(lg) {
				content: '';
			}
		}

		&:before {
			top: 0;
			left: 0;
		}

		&:after {
			top: 100%;
			right: 0;
		}

		&:hover {
			color: $white;

			@include media-breakpoint-up(lg) {
				color: $black;
			}

			&:before,
			&:after {
				width: 100%;
				opacity: 1;
			}
		}
	}
}

.mobile-menu-overlay {
	position: fixed;
	top: 70px;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100vw;
	height: calc(100vh - 70px);
	height: calc(var(--vh, 1vh) * 100 - 70px);
	background-color: rgba($black, 0.4);
	z-index: $zindex-mobile-menu-overlay;
	opacity: 0;
	visibility: hidden;
	transition: opacity 0.5s, visibility 0.5s;

	@include media-breakpoint-up(lg) {
		display: none;
	}

	&.menu-active {
		opacity: 1;
		visibility: visible;
	}
}