.our-focus-list {
	@include listreset;

	font-size: 20px;

	@include media-breakpoint-up(md) {
		font-size: 22px;
	}

	@include media-breakpoint-up(xl) {
		font-size: 26px;
	}

	&__item {
		position: relative;
		background-color: $white;
		padding: 10px 15px 10px 50px;

		@include media-breakpoint-up(md) {
			padding: 12px 15px 12px 50px;
		}

		&:not(:last-child) {
			margin-bottom: 12px;
		}

		&:before {
			content: '';
			position: absolute;
			top: 21px;
			left: 20px;
			width: 11px;
			height: 11px;
			border-radius: 2px;
			background-color: $primary;

			@include media-breakpoint-up(md) {
				top: 24px;
			}

			@include media-breakpoint-up(xl) {
				top: 27px;
			}
		}
	}
}