.section {
	padding: 50px 0;

	@include media-breakpoint-up(md) {
		padding: 80px 0;
	}

	@include media-breakpoint-up(lg) {
		padding: 120px 0;
	}

	&.blog-card-compensation {
		padding-bottom: 20px;

		@include media-breakpoint-up(md) {
			padding-bottom: 40px;;
		}

		@include media-breakpoint-up(lg) {
			padding-bottom: 60px;
		}
	}

	&-welcome {
		position: relative;
		padding: 130px 0;

		@include media-breakpoint-up(sm) {
			padding: 130px 0 150px;
		}

		@include media-breakpoint-up(md) {
			padding: 150px 0 200px;
		}

		@include media-breakpoint-up(lg) {
			padding: 180px 0 220px;
		}

		@include media-breakpoint-up(xl) {
			padding: 215px 0 355px;
		}

		&__main-content {
			max-width: 750px;
		}

		&__bg {
			@include bgCover;
			@include absoluteCover;

			pointer-events: none;
		}

		&__main-wrap {
			position: relative;
			z-index: 5;
		}
	}

	&-welcome-small {
		position: relative;
		text-align: center;
		padding: 135px 0 75px;

		@include media-breakpoint-up(lg) {
			padding-top: 155px;
		}

		&__date-badge {
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translate(-50%, 0);
			display: inline-block;
			background-color: $primary;
			color: $white;
			font-size: 14px;
			font-weight: 700;
			padding: 0.25em 1em;

			@include media-breakpoint-up(md) {
				font-size: 18px;
			}
		}

		&__bg {
			@include absoluteCover;
			@include bgCover;

			pointer-events: none;

			&:before {
				@include absoluteCover;

				content: '';
				display: block;
				background-color: rgba($black, 0.3);
			}

			&:after {
				@include absoluteCover;

				content: '';
				display: block;
				background-color: rgba($white, 0.85);
			}
		}

		&__main-wrap {
			position: relative;
			z-index: 5;
		}
	}

	&-selected-text {
		position: relative;

		@include media-breakpoint-up(lg) {
			padding: 90px 0;
		}

		@include media-breakpoint-up(xl) {
			padding: 105px 0 115px;
		}

		&__text {
			text-align: center;
			max-width: 920px;
			margin: 0 auto;
			font-size: 18px;
			line-height: 1.5;

			@include media-breakpoint-up(md) {
				font-size: 20px;
			}

			@include media-breakpoint-up(lg) {
				font-size: 23px;
			}

			p {
				margin-bottom: 35px;
			}
		}

		&__bg {
			@include absoluteCover;
			@include bgCover;

			pointer-events: none;
		}

		&__main-wrap {
			position: relative;
			z-index: 5;
		}
	}

	&-about-us {
		position: relative;
		color: $white;
		background-color: $black;

		@include media-breakpoint-up(lg) {
			padding: 110px 0;
		}

		&__main-content {
			max-width: 755px;
		}

		&__description {
			margin-bottom: 30px;
		}

		&__bg {
			@include absoluteCover;
			@include bgCover;

			pointer-events: none;
		}

		&__main-wrap {
			position: relative;
			z-index: 5;
		}
	}

	&-our-focus {
		position: relative;
		padding-bottom: 250px;

		@include media-breakpoint-up(lg) {
			padding-top: 105px;
			padding-bottom: 295px;
		}

		+ .section-industries {
			padding: 0 0 40px 0;
			margin-top: -160px;

			@include media-breakpoint-up(md) {
				padding: 0 0 90px 0;
			}
		}

		&__main-content {
			@include media-breakpoint-up(lg) {
				max-width: 465px;
			}
		}

		&__description {
			margin-bottom: 35px;
		}

		&__list {
			margin: 50px 0 0 0;

			@include media-breakpoint-up(lg) {
				margin: 95px 0 0 15px;
				max-width: 485px;
			}
		}

		&__bg {
			@include absoluteCover;
			@include bgCover;

			pointer-events: none;
		}

		&__main-wrap {
			position: relative;
			z-index: 5;
		}
	}

	&-industries {
		position: relative;

		&__block {
			background-color: $white;
			border-radius: 8px;
			overflow: hidden;

			@include media-breakpoint-up(lg) {
				display: flex;
				flex-wrap: wrap;
			}
		}

		&__title-block {
			padding: 15px;

			@include media-breakpoint-up(sm) {
				padding: 25px;
			}

			@include media-breakpoint-up(md) {
				padding: 40px;
			}

			@include media-breakpoint-up(lg) {
				flex: 0 0 34%;
				max-width: 34%;
				text-align: right;
				padding: 85px 40px;
			}

			@include media-breakpoint-up(xl) {
				padding: 110px 70px;
			}
		}

		&__content-block {
			position: relative;
			padding: 15px;
			color: $white;
			background-color: $black;

			@include media-breakpoint-up(sm) {
				padding: 25px;
			}

			@include media-breakpoint-up(md) {
				padding: 60px 40px;
			}

			@include media-breakpoint-up(lg) {
				flex: 0 0 66%;
				max-width: 66%;
				padding: 85px 40px;
			}

			@include media-breakpoint-up(xl) {
				padding: 110px 70px;
			}
		}

		&__content-block-bg {
			@include absoluteCover;
			@include bgCover;

			pointer-events: none;
		}

		&__list {
			position: relative;
			z-index: 5;

			@include media-breakpoint-up(md) {
				column-count: 2;
			}
		}
	}

	&-phases-of-our-process {
		&__list {
			.section-phases-of-our-process__item {
				&:nth-child(2n) {
					background-color: lighten($black, 5%);

					.section-phases-of-our-process__item-content {
						@include media-breakpoint-up(md) {
							margin-left: auto;
						}
					}
				}
			}
		}

		&__item {
			position: relative;
			color: $white;
			background-color: $black;

			@include media-breakpoint-up(xl) {
				padding: 150px 0;
			}
		}

		&__item-content {
			max-width: 530px;
		}

		&__item-bg {
			@include absoluteCover;
			@include bgCover;

			pointer-events: none;
		}

		&__item-main-wrap {
			position: relative;
			z-index: 5;
		}
	}

	&-contact-us {
		@include media-breakpoint-up(xl) {
			padding: 130px 0 125px;
		}

		&__main-content {
			max-width: 380px;
		}

		&__address {
			margin-bottom: 30px;
		}

		&__form-block {
			padding-top: 60px;

			@include media-breakpoint-up(md) {
				padding-top: 80px;
			}
		}
	}

	&-default {
		position: relative;

		ul {
			margin: 30px 0;
			padding-left: 0;
			list-style-type: disc;
			list-style-position: inside;
		}

		ol {
			margin: 30px 0;
			padding-left: 0;
			list-style-position: inside;
		}

		blockquote {
			margin: 40px 0;
			padding-left: 50px;
			border-left: 2px solid $primary;

			p {
				&:last-child {
					margin-bottom: 0;
				}
			}
		}

		a {
			&:not([class]) {
				transition: color 0.3s;
				text-decoration: underline;

				&:hover {
					text-decoration: none;
				}
			}
		}

		del {
			text-decoration: line-through;
		}

		hr {
			margin: 30px 0;
			border-color: rgba($black, 0.1);
		}

		img {
			&.aligncenter {
				@include media-breakpoint-up(xl) {
					margin-left: -100px;
					margin-right: -100px;
					max-width: calc(100% + 200px);
					width: calc(100% + 200px);
				}
			}
		}
	}
}