@include title-sizes();

.text-block,
.remove-last-child-margin {
	> * {
		&:last-child {
			margin-bottom: 0;
		}
	}

	&.row {
		> * {
			&.col,
			&[class^="col-"] {
				&:last-child {
					> * {
						margin-bottom: 0;
					}
				}
			}
		}
	}
}

.text-sm {
	font-size: $font-size-sm;
	line-height: $line-height-sm;
}

h1, .h1 {
	margin-bottom: 20px;

	&.text-lettering-animation {
		line-height: 0.6;

		.word {
			line-height: 1.2;
		}
	}
}

h2, .h2 {
	line-height: 1.2;
	margin-bottom: 10px;
}

h3, .h3 {
	line-height: 1.2;
	margin-bottom: 25px;
}

h4, .h4 {

}

h5, .h5 {
	line-height: 1.33;
	margin-bottom: 15px;

	@include media-breakpoint-up(md) {
		margin-bottom: 20px;
	}
}

h6, .h6 {
	margin-bottom: 15px;
}

.address-text {
	font-size: 19px;
	font-style: normal;
	color: $gray-500;
	line-height: 1.6;
	margin-bottom: 0;
}

.list-with-dots {
	@include listreset;

	font-size: 18px;
	line-height: 1.739;
	font-weight: 700;

	@include media-breakpoint-up(md) {
		font-size: 20px;
	}

	@include media-breakpoint-up(lg) {
		font-size: 23px;
	}

	li {
		position: relative;
		padding-left: 15px;

		&:not(:last-child) {
			margin-bottom: 2px;
		}

		&:before {
			content: '•';
			display: inline-block;
			position: absolute;
			top: 9px;
			left: 0;
			font-size: 10px;
			line-height: 1;

			@include media-breakpoint-up(md) {
				top: 12px;
			}

			@include media-breakpoint-up(lg) {
				top: 13px;
			}
		}
	}
}

.list-with-squares {
	@include listreset;

	font-size: 18px;
	line-height: 1.6136;

	@include media-breakpoint-up(md) {
		font-size: 20px;
	}

	@include media-breakpoint-up(lg) {
		font-size: 22px;
	}

	li {
		position: relative;
		padding-left: 24px;

		&:before {
			content: '';
			position: absolute;
			top: 11px;
			left: 0;
			display: block;
			width: 8px;
			height: 8px;
			background-color: $white;
			border-radius: 2px;

			@include media-breakpoint-up(md) {
				width: 11px;
				height: 11px;
				top: 11px;
			}

			@include media-breakpoint-up(lg) {
				top: 13px;
			}
		}
	}
}

.expected-read-time {
	position: absolute;
	top: 10px;
	left: 0;
	right: 0;
	width: 100%;
	text-align: center;
	font-size: 14px;
	font-style: italic;
	padding: 0 $grid-gutter-width * 0.5;

	@include media-breakpoint-up(sm) {
		font-size: 16px;
	}
}