.pagination {
	padding-top: 15px;

	@include media-breakpoint-up(sm) {
		padding-top: 30px;
	}

	.page-item {
		margin: 2px;
	}

	.page-link {
		@include flexCenter;
		@include sizeElement(35px);

		transition: color 0.3s, border-color 0.3s, background-color 0.3s;

		@include media-breakpoint-up(sm) {
			@include sizeElement(45px);
		}

		&[aria-label="Previous"] {
			> span {
				display: inline-flex;

				&:after {
					@include border-arrow(6, 10, currentColor, l);

					content: '';
					display: inline-block;
				}
			}
		}

		&[aria-label="Next"] {
			> span {
				display: inline-flex;

				&:after {
					@include border-arrow(6, 10, currentColor, r);

					content: '';
					display: inline-block;
				}
			}
		}
	}
}