.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	border: 0;
	clip: rect(0 0 0 0);
	overflow: hidden;
}

.overflow-hidden {
	overflow: hidden !important;
}

.ofi {
	@include absoluteCover;

	object-fit: cover;
	font-family: 'object-fit: cover;';
}

img {
	&.alignleft {
		display: block;
		margin: 0 auto 30px;

		@include media-breakpoint-up(lg) {
			float: left;
			margin: 30px 30px 30px 0;
		}
	}

	&.alignright {
		display: block;
		margin: 0 auto 30px;

		@include media-breakpoint-up(lg) {
			float: right;
			margin: 30px 0 30px 30px;
		}
	}

	&.aligncenter {
		display: block;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 30px;
	}
}

.bg-gray-100 {
	background-color: $gray-100 !important;
}

.resize-animation-stopper * {
	animation: none !important;
	transition: none !important;
}